import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
import {ApplyRanking,ApplySon} from 'api/user'
export default{
	components:{
		UniLoadMore
	},
	data(){
		return{
			tabs:['分销商佣金排名','子分销商数排名'],
			index: 0,
			baseUrl:this.$store.state.SRC+"/config/app",
			list:[],
			data:{},
			state:{},
			page:1,
			pageCount:1,
			moreStatus:'',
			load:false
		}
	},
	onPullDownRefresh() {
		if(this.index == 0){
			this.getList()	
		}else{
			this.getListSon()	
		}
	},
	/*滚动加载*/
	onReachBottom(){
		this.page++
		if(this.page<=this.pageCount){
			if(this.index == 0){
				this.getList(this.page)	
			}else{
				this.getListSon(this.page)	
			}	
		}	
	},
	methods:{
		changeTabs(e){
			if(e.tab === '分销商佣金排名'){
				this.getList()
			}else if(e.tab === '子分销商数排名'){
				this.getListSon()
			}
		},
		getListSon(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
			}
			this.data = {}
			this.moreStatus = 'loading'
			ApplySon({
				pageindex:this.page,
				pagesize:15,
			}).then(res=>{
				uni.stopPullDownRefresh();
				this.pageCount = res.pageCount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.data = res
					this.list = this.list.concat(res.data)
					
				}
			})		
		},
		getList(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
			}
			this.data = {}
			this.moreStatus = 'loading'
			ApplyRanking({
				pageindex:this.page,
				pagesize:15,
			}).then(res=>{
				uni.stopPullDownRefresh();
				this.pageCount = res.pageCount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.data = res
					this.list = this.list.concat(res.data)
					this.load = true
				}
			})		
		}
	},
	onLoad() {
		this.$store.ready(state=>{
			this.state = state
			this.getList()
		})
		
		
	}
}
